h1,
h2,
h3,
h4,
h5 {
  font-weight: 800;
}
body,
html {
  font-family: "Nunito", sans-serif;
  background-color: #ffdf8c;
}

.wrapper {
  max-width: 1400px;
  width: 100%;
  margin: 30px auto;
  padding: 50px 0;
  background: #fff;
  border-radius: 15px;
  overflow: hidden;
  @media screen and (max-width: 550px) {
    padding-top: 30px;
  }
}
